import React from 'react';

import Apase from '../../public/apase.png';
import Leluxe from '../../public/Leluxe.png';
import Poem from '../../public/poem.png';
import Henma from '../../public/henma.png';

import './Partners.css';

const Partners = () => {
    return (
        <div className="Partners">
            <h1>Our Partners</h1>
            <hr />
                <div className="Partners__Logo">
                    <img src={Apase} alt="Partner"/>
                    <img src={Leluxe} alt="Partner"/>
                    <img src={Poem} alt="Partner"/>
                    <img src={Henma} alt="Partner"/>
                </div>
        </div>
    )
}

export default Partners;
