import React from 'react';

import UI from '../../public/ui.svg';
import Team from '../../public/team.png';

import './About.css';

const About = () => {
    return (
        <div className="About__Main">
            <div className="About">
                <div className="About__top">
                    <h1>WHY<br /> <span>LUCIG?</span></h1>
                    <p>We design your dream to life. And why are we doing this? Because we care about the future you see. <br />
                        Our team is full of young, vibrant minds who are looking forward to changing the world with you. Every project we take, we assign a project manager who supervises the day-to-day development of your project.
                        As the world of tech evolves, we make sure that our Engineers stay on top of their game as the code written today must still be built on over time.
                    </p>
                </div>
                <div>
                    <img src={UI} alt="Who we are" />
                </div>
            </div>
            <div className="Join">
                <h1>JOIN OUR<br/> COMMUNITY</h1>
                <div>
                    <p>
                        Do you feel that you've got what it takes to be a Senior Software Engineer at LucigHub? Join our growing community while we assess your knowledge.
                        Be part of a team who is changing the world with their expertise.
                    </p>
                </div>
                <button><a href="/career" target="_blank">Apply as an Engineer</a></button>
            </div>
            <div className="Engineer">
                <div>
                    <h1>YOUR IDEA, <span>OUR PROJECT</span></h1>
                    <p>Why sleep on your ideas when we can achieve that for you. All you need to do is send us an email detailing what the application does.</p>
                    <button><a href="mailto:hire@lucigtech.com">Hire an Enigineer</a></button>
                </div>
                <div>
                    <img src={Team} alt="Lucig Team" />
                </div>
            </div>
        </div>
    )
}

export default About;
