import React, { useState } from 'react';
import axios from 'axios';

import Agro from '../../public/AgroAfrica.png';

import './Hire.css';

const Hire = props =>{

 let option_id = [
        { id: 1, name: 'us', value: 'United States (US)' }, 
        { id: 2, name: 'ng',value: 'Nigeria'},
        { id: 3, name: 'uk', value: 'United Kingdom(UK)' },
        { id: 4, name: 'ca', value: 'Canada' },
        { id: 5, name: 'sa', value: 'South Africa' },
        { id: 6, name: 'bn', value: 'Republic of Benin' },
        { id: 7, name: 'gh', value: 'Ghana' },
        { id: 8, name: 'tg', value: 'Togo' },
        { id: 9, name: 'ic', value: 'Ivory Coast' },
        { id: 10, name: 'gm', value: 'Gambia' },
        { id: 11, name: 'ukr', value: 'Ukraine' },
        { id: 12, name: 'rs', value: 'Russia' },
        { id: 13, name: 'au', value: 'Austria' },
        { id: 14, name: 'aus', value: 'Australia' },
        { id: 15, name: 'ky', value: 'Kenya' },
        { id: 16, name: 'cam', value: 'Cameroun' },
        { id: 17, name: 'fr', value: 'France' },
        { id: 18, name: 'gmn', value: 'Germany' },
        { id: 19, name: 'chi', value: 'China' },
        { id: 20, name: 'sk', value: 'South Koria' },
        { id: 21, name: 'tz', value: 'Tanzania' },
        { id: 22, name: 'bos', value: 'Boswana' },
        { id: 23, name: 'irl', value: 'Ireland' },
        { id: 24, name: 'irn', value: 'Iran' },
        { id: 25, name: 'mali', value: 'Mali' },
        { id: 26, name: 'moz', value: 'Mozambiq' },
        { id: 27, name: 'chile', value: 'Chile' },
        { id: 28, name: 'brz', value: 'Brazil' },
        { id: 29, name: 'swed', value: 'Sweden' },
        { id: 30, name: 'hungary', value: 'Hungary' },
        { id: 31, name: 'india', value: 'India' },
    ];

    let options = option_id.map(country => {
        return <option key={country.id} name={country.name}>{country.value}</option>
    });

   

    const [ contact, setContact ] = useState({
        name : "",
        email : "",
        phone : "",
        company : "",
        country : "",
        duration : "",
        desc : "",
    });

    const {  name, email, phone, company, country, duration, desc }  = contact;

    const onChange = e =>{
        setContact({ ...contact, [e.target.name] : e.target.value  });
    }

    const onSubmit = e =>{
        e.preventDefault();
        // setContact({message : "Sending..."});

        axios.post('http://localhost:5000/api/message/hire', contact).then(data =>{
            console.log(data);
        }).catch(error =>{
            console.log(error)
        })

        console.log(contact);

        setContact({
            name : "",
            email : "",
            phone : "",
            company : "",
            country : "",
            duration : "",
            desc : "",
        })

    }

    return(
        <div className="Hire">
            <div className="Hire__Form">
                <form onSubmit={onSubmit}>
                    <h1>Hire an Engineer/Designer</h1>
                    <label>Name: </label>
                    <input type="text" name="name" value={name} onChange={onChange} required />
                    <label>Email: </label>
                    <input type="email" name="email" value={email} onChange={onChange} required />
                    <label>Phone: </label>
                    <input type="number" name="phone" value={phone} onChange={onChange} required />
                    <label>Company: </label>
                    <input type="text" name="company" value={company} onChange={onChange} required />
                    <label>Country: </label>
                    <select name="country" defaultValue ={country} onChange={onChange} >
                        {options}
                    </select>
                    <label>Project Duration: <span className="months">* In Month(s)</span></label>
                    <input type="number" name="duration" value={duration} onChange={onChange} required />
                    <label>Project Description: </label>
                    <textarea name="desc" row="140" col="450" value={desc} onChange={onChange} required></textarea>
                    <p>By clicking on "Let's start", you have agreed to our Terms &amp; Conditions</p>
                    <button type="submit" className="Hire__btn">Start Project</button>
                </form>
            </div>
            <div className="Hire__Side">
                <img src={Agro} alt="On the Side" />
                <p>A tress does not make a forest, that's why we are willing to cowork with your team so that we can solve that problem together.</p>
            </div>
        </div>
    );
}

export default Hire;