import React from 'react';

import Deploy from '../../public/mobile_development.svg';

import './Teamwork.css';

const Teamwork = () => {
    return (
        <div className="Teamwork">
            <div className="Teamwork_Main">
                <h1>WE DEPLOY <span className="Teamwork_msg">SCALABLE APPLICATIONS</span></h1>
                <img src={Deploy} alt="Scalable Applications"/>
            </div>
        </div>
    )
}

export default Teamwork;
