import React from 'react';

import './Header.css';

export const Header = () => {
    return (
        <div className="Header fluid-container">

            <div className="Headline">
                <h1>LucigHub brings you the worlds <span className="Talent">leading software engineers.</span></h1>
                <h3>We provide a network of top-tier engineers from countries you havent considered. But you probably should.</h3>
            </div>
            <button><a href="/services">Learn More <i className="fas fa-arrow-circle-right"></i></a></button>

            <div className="Lower_header">
                <p>We will hold your hands from the research stage of your project to the deployment stage so that the journey will be a hitch-free ride for you.</p>
            </div>
        </div>
    )
}

export default Header;
