import React from 'react';
import './Mission.css';

export const Mission = () => {
    return (
        <div className="Mission">
            <div className="Mission_Goal">
                <h1>Our Goal</h1>
                <hr />
                <div className="Mission_p">
                    <p>
                    Ten (10) years ago before the advent of todays and evolving technology, we believe there were many issues that affected our societies which today technology has helped solved but there are still more to be done. The firm of LUCIGHUB was principally established to work with Individuals, Private and Public Organizations, Financial Institutions, the Government, Non Governmental Organizations and Resource People in our society so as to tackle as many problems in the society using technology.
                    </p>
                </div>
            </div>
        </div>
    )
}

export default Mission;
