import React, {useState} from 'react';
import axios from 'axios';

import './Experience.css';

import Developer from  '../../public/developer.jpg';

const Experience = () => {

    let option_id = [
        { id: 1, name: 'us', value: 'United States (US)' }, 
        { id: 2, name: 'ng',value: 'Nigeria'},
        { id: 3, name: 'uk', value: 'United Kingdom(UK)' },
        { id: 4, name: 'ca', value: 'Canada' },
        { id: 5, name: 'sa', value: 'South Africa' },
        { id: 6, name: 'bn', value: 'Republic of Benin' },
        { id: 7, name: 'gh', value: 'Ghana' },
        { id: 8, name: 'tg', value: 'Togo' },
        { id: 9, name: 'ic', value: 'Ivory Coast' },
        { id: 10, name: 'gm', value: 'Gambia' },
        { id: 11, name: 'ukr', value: 'Ukraine' },
        { id: 12, name: 'rs', value: 'Russia' },
        { id: 13, name: 'au', value: 'Austria' },
        { id: 14, name: 'aus', value: 'Australia' },
        { id: 15, name: 'ky', value: 'Kenya' },
        { id: 16, name: 'cam', value: 'Cameroun' },
        { id: 17, name: 'fr', value: 'France' },
        { id: 18, name: 'gmn', value: 'Germany' },
        { id: 19, name: 'chi', value: 'China' },
        { id: 20, name: 'sk', value: 'South Koria' },
        { id: 21, name: 'tz', value: 'Tanzania' },
        { id: 22, name: 'bos', value: 'Boswana' },
        { id: 23, name: 'irl', value: 'Ireland' },
        { id: 24, name: 'irn', value: 'Iran' },
        { id: 25, name: 'mali', value: 'Mali' },
        { id: 26, name: 'moz', value: 'Mozambiq' },
        { id: 27, name: 'chile', value: 'Chile' },
        { id: 28, name: 'brz', value: 'Brazil' },
        { id: 29, name: 'swed', value: 'Sweden' },
        { id: 30, name: 'hungary', value: 'Hungary' },
        { id: 31, name: 'india', value: 'India' },
    ];

    let options = option_id.map(country => {
        return <option key={country.id} name={country.name}>{country.value}</option>
    });

    const [ hire, setHire ] = useState({
        name: "",
        email: "",
        phone: "",
        github: "",
        website: "",
        country: ""
    });

    const { name, email, phone, github, website, country  } = hire;

    const onChange = e =>{
        setHire({...hire, [e.target.name] : e.target.value });
    }

    const onSubmit = e =>{
        e.preventDefault();
        // console.log(hire);

        axios.post('https://localhost:5000/api/message/apply', hire)
            .then(data =>{
                console.log(data)
        })
            .catch(error =>{
                console.log(error)
            });

            setHire({
                name: "",
                email: "",
                phone: "",
                github: "",
                website: "",
                country: "Select a Country"
            })
    }

    return(
        <div className="Experience">
            <div className="Experience__Topic">
                <h1>Do you have what it takes to work with us at LucigHub?</h1>
                <h3>You have to have at least 5 - 6 years experience as a Software Engineer.</h3>
            </div>

            <div className="Experience__Lower">

                <div className="Experience__Form">
                    <form onSubmit={onSubmit}>
                        <label htmlFor="name">Full Name: </label>
                        <input type="text" name="name" onChange={onChange} value={name} required />
                        <label htmlFor="email">Email: </label>
                        <input type="text" name="email" onChange={onChange} value={email} required />
                        <label htmlFor="phone">Phone: </label>
                        <input type="number" name="phone" onChange={onChange} value={phone} required />
                        <label htmlFor="github">Github Link</label>
                        <input type="text" name="github" onChange={onChange} value={github} required />
                        <label htmlFor="website">Portfolio Website: </label>
                        <input type="text"  name="website" onChange={onChange} value={website} required />
                        <label htmlFor="country">Country: </label>
                        <select onChange={onChange} defaultValue={country} name="country">
                            {options}
                        </select><br />
                        <button type="submit" className="Experience__btn">Apply</button>
                    </form>
                </div>

                <div>
                    <div>
                        <img src={Developer} alt="BrainStorming" className="Experience__Img" />
                        <img src={Developer} alt="BrainStorming" className="Experience__Img__Two" />
                    </div>
                </div>
            </div>

            <div className="Cred">
                    <div className="Cred__Single">
                        <h1>What we look for:</h1>
                        <p>Our Software Engineers have at least 5 - 6 years working experience with vast knowlege in JavaScript, 
                            Python, MySQL, NoSQL, C++, Java, Ruby, Swift and many more.
                        </p>
                    </div>
                    <div className="Cred__Single">
                        <h1>Why LucigHub:</h1>
                            <p>
                                We are one of the world leading software companies that get the job done for you with our software skills. 
                                Our expertise is something you can trust.
                            </p>
                    </div>

                    <div className="Cred__Single">
                        <h1>Creative Thinkers:</h1>
                            <p>
                                All of our Engineers and designers come together to have a creative brain stormy session of given our partners 
                                best ideas possible.
                            </p>
                    </div>
                </div>

                <div className="Cred Cred__New">
                    <div className="Cred__Single">
                        <h1>Bring Ideas to life:</h1>
                        <p>
                            One of the tasks that is expected from our Engineers is the ability to bring our partner's ideas to life.
                        </p>
                    </div>
                    <div className="Cred__Single">
                        <h1>Evolve:</h1>
                            <p>
                                The world of tech waits for no man, so you have to be update to date on the latest stack out there. Build with best practices.
                            </p>
                    </div>

                    <div className="Cred__Single">
                        <h1>Assessment:</h1>
                            <p>
                                Be ready to take our assessment which will be determined by the team of LucigHub
                            </p>
                    </div>
                </div>

        </div>
    );
}

export default Experience;