import React from 'react';

import Developer from '../../public/developers.svg';

import Python from '../../public/Python.png';
import Java from '../../public/Java.png';
import Swift from '../../public/Swift.png';
import JavaScript from '../../public/JS.png';
import Angular from '../../public/Angular.png';
import ReactJS from '../../public/ReactJS.png';
import Figma from '../../public/Figma.png';
import Xd from '../../public/Xd.png';
import Kotlin from '../../public/Kotlin.png';
import NodeJS from '../../public/NodeJS.png';
import PHPs from '../../public/PHP.png';
import MySQL from '../../public/MySQL.png';

import './Developers.css';

const Developers = () => {
    return (
        <div className="Developers">
            <h1>World Class <span>Developers</span></h1>
            <hr />
            <div className="Developers_info">
                <img src={Developer} alt="Developers" />
                <div  className="Developers_desc">
                    <p>
                    LucigHub is one of the foremost Information Technology Solutions Companies in Africa. We have evolved from a small organization pushing boxes to customers to a company providing enterprise ICT services to mid range and large scale companies across Nigeria, Rep of Benin, United Kingdom.
                   
                   <br />
                    <table>
                        <tr>
                            <td><img src={Python} alt="One of the Languages we are expert in" /></td>
                            <td><img src={Java} alt="One of the Languages we are expert in" width="100%" /></td>
                            <td><img src={Swift} alt="One of the Languages we are expert in" /></td>
                            <td><img src={JavaScript} alt="One of the Languages we are expert in" /></td>
                            <td><img src={Angular} alt="One of the Languages we are expert in" /></td>
                            <td><img src={ReactJS} alt="One of the Languages we are expert in" /></td>
                        </tr>
                        <tr>
                            <td><img src={Figma} alt="One of the Languages we are expert in" /></td>
                            <td><img src={Xd} alt="One of the Languages we are expert in" /></td>
                            <td><img src={Kotlin} alt="One of the Languages we are expert in" /></td>
                            <td><img src={NodeJS} alt="One of the Languages we are expert in" width="100%"  /></td>
                            <td><img src={PHPs} alt="One of the Languages we are expert in" /></td>
                            <td><img src={MySQL} alt="One of the Languages we are expert in" width="100%" /></td>
                        </tr>
                    </table>
                    </p>
                </div>
            </div>
        </div>
    )
}

export default Developers;
