import React, {Fragment} from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import Navigation from './components/navigation/Navigation';
import Home from './containers/Home/Home';
import Services from './containers/Services/Services';
import About from './containers/about/About';
import Career from './containers/career/Career';
import Press from './containers/press/Press';
import SSAs from './containers/ssa/SSA';
import Conditions from './containers/conditions/Conditions';
import CPs from './containers/cp/CP';
import LSAs from './containers/lsa/LSA';
import NotFound from './containers/404/NotFound';
import Contact from './containers/contact/Contact';
import Hire from './containers/hire/Hire';
import Experience from './containers/experience/Experience';
import Footer from './components/footer/Footer';

import 'bootstrap/dist/css/bootstrap.min.css';

import './App.css';

function App() {
  return (
    <div className="App">
      <Router>
        <Fragment>
          <Route component={Navigation} />
          <Switch>
            <Route path="/" exact component={Home} />
            <Route path="/services" exact component={Services} />
            <Route path="/about" exact component={About} />
            <Route path="/hire-developer" exact component={Contact} />
            <Route path="/careers" exact component={Career} />
            <Route path="/press" exact component={Press}/>
            <Route path="/ssa" exact component={SSAs} />
            <Route path="/tac" exact component={Conditions} />
            <Route path="/cp" exact component={CPs} />
            <Route path="/lsa" exact component={LSAs} />
            <Route path="/hire" exact component={Hire} />
            <Route path="/apply" exact component={ Experience } />
            <Route path="*" exact component={NotFound} />
          </Switch>
          <Route component={Footer} />
        </Fragment>
      </Router>
    </div>
  );
}

export default App;
