import React from 'react';

import './Contact.css';

const Contact = () =>{
    return(
        <div className="Contact">
            <h1>Welcome to the Hiring Hub</h1>
            <div className="Hire__Team">
                <div className="Hire__Single">
                    <h2>Frontend Engineer</h2>
                    <p>Hire just our frontend Engineer for your upcoming project.</p>
                    <a href="/hire"> Hire Now <i className="fas fa-arrow-circle-right"></i> </a>
                </div>

                <div className="Hire__Single">
                    <h2>Backend Engineer</h2>
                    <p>Hire just our backend Engineer for your upcoming project.</p>
                    <a href="/hire-frontend"> Hire Now <i className="fas fa-arrow-circle-right"></i> </a>
                </div>

                <div className="Hire__Single">
                    <h2>UX/UI Designer</h2>
                    <p>Hire just our UX/UI designer for your upcoming project.</p>
                    <a href="/hire-frontend"> Hire Now <i className="fas fa-arrow-circle-right"></i> </a>
                </div>

                <div className="Hire__Single">
                    <h2>Full Stack Engineer</h2>
                    <p>Hire full stack Engineer for your upcoming project.</p>
                    <a href="/hire-frontend"> Hire Now <i className="fas fa-arrow-circle-right"></i> </a>
                </div>

                <div className="Hire__Single">
                    <h2>Full Team</h2>
                    <p>Hire full team for your upcoming project. Tell us about your project.</p>
                    <a href="/hire-frontend"> Hire Now <i className="fas fa-arrow-circle-right"></i> </a>
                </div>

                <div className="Hire__Single">
                    <h2>Why Hire from Us</h2>
                    <p>Every Member has a minimum of four(4) years experience.</p>
                    <a href="/about"> Check Now <i className="fas fa-arrow-circle-right"></i> </a>
                </div>

            </div>
            <p>When you hire an Engineer/Designer, note that you will have to share everything (*Functionalities of the project) with us to understand what the project entails. You might be asked to upload a file that explains everything to us. For more questions, visit our <a href="/faq">FAQ</a></p>
        </div>
    );
}

export default Contact;