import React from 'react';

import Problem from '../../public/problems.svg';

import './Clientproject.css';

export const Clientproject = () => {
    return (
        <div className="Clientproject">
            <div className="ClientText">
                <h1><span>Solving</span> Problems <span>Together.</span></h1>
                <p> <em>We are fully committed to providing excellent services using latest and advanced languages in the world of tech to render a professional service to our clients. Through Our Team Work, we shall continue to improve performance and more so towards Total Customers satisfaction. </em></p>
                <button>Hire a developer</button>
            </div>
            <div>
                <img src={Problem} alt="Solving Problems" />
            </div>
        </div>
    )
}

export default Clientproject;
